import React from 'react';
import PropTypes from 'prop-types';

export default class RadioGroup extends React.Component {
  constructor(props) {
    super(props);
  }
  onChange = (e) => {
    this.props.onChange(e);
    console.log(e);
  }
  render() {
    const {name} = this.props;
    return (
      <div class="radios">
        <label>
          <input type="radio" onChange={this.onChange} name={name} value="1"  />
          火车站
        </label>
        <label>
          <input type="radio" onChange={this.onChange} name={name} value="2"  />
          高铁站
        </label>
        <label>
          <input type="radio" onChange={this.onChange} name={name} value="3"  />
          飞机场
        </label>
      </div>
    );
  }
}
RadioGroup.propTypes = {
  onChange: PropTypes.func,
  value: PropTypes.any,
  name: PropTypes.string,
  radiosData: PropTypes.arrayOf()
}